@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-Heavy.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Roboto Slab';
  src: url("../fonts/RobotoSlab-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Lato-Italic';
  src: url("../fonts/Lato-HeavyItalic.woff2") format("woff2");
  font-weight: 900;
  font-style: italic;
  font-display: swap;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  color: #4b5157;
  background-color: #E5E5E5;
}

.page--active {
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
}

button {
  border: none;
  background: transparent;
  cursor: pointer;
}

button:focus {
  outline: none;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1170px;
}

.container-s {
  margin: 0 auto;
  max-width: 946px;
}

.js-validate-error-label {
  position: absolute;
  left: 0;
  bottom: -16px;
  font-size: 14px;
}

.js-validate-error-field {
  border: 1px solid #D9DADB !important;
}

.error {
  background-color: #fff;
  padding: 100px;
}

.error__title {
  text-align: center;
  color: #262D33;
  font-size: 40px;
}

.to-top {
  outline: none;
  position: fixed;
  right: 20px;
  bottom: 20px;
  width: 0px;
  height: 0px;
  z-index: 999;
  transition: width 0.3s ease, height 0.3s ease;
}

.to-top__img {
  width: 100%;
  height: 100%;
  fill: #4592ff;
  transition: fill 0.3s ease;
}

.to-top__img:hover {
  fill: #0047ab;
  transition: fill 0.3s ease;
}

.visible {
  transition: width 0.3s ease, height 0.3s ease;
  width: 50px;
  height: 50px;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.header {
  background-color: #262D33;
  padding: 15px 0;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  width: 163px;
  height: 40px;
}

.header--light {
  background-color: #fff;
}

.nav {
  color: #ffffff;
}

.nav__list {
  display: flex;
}

.nav__link {
  position: relative;
  font-family: "Lato";
  font-weight: 900;
  font-size: 14px;
  padding: 25px 15px;
}

.nav--light {
  color: #939699;
}

@media (hover: hover) and (pointer: fine) {
  .nav__link {
    transition: background-color 0.3s ease;
  }
  .nav__link:hover {
    background-color: #3c4750;
    transition: background-color 0.3s ease;
  }
}

.search {
  position: relative;
  width: 263px;
  height: 39px;
  background-color: #656B70;
  border-radius: 10px;
  overflow: hidden;
}

.search--light {
  background-color: #F5F5F5;
}

.search__input {
  outline: none;
  padding-left: 23px;
  padding-right: 75px;
  padding: 9px 75px 9px 25px;
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
}

.search__input::placeholder {
  color: #939699;
  font-size: 14px;
  line-height: 20px;
}

.search__button {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 71px;
}

.search__button:hover {
  background-color: #4d5155;
}

.search__button-img {
  width: 17.5px;
  height: 18px;
}

.hero__bg {
  position: absolute;
  background-color: #262D33;
  height: 475px;
  width: 100%;
  margin-top: -30px;
}

.hero__wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 570px;
  margin-top: 30px;
  margin-bottom: 30px;
  border-radius: 10px;
  overflow: hidden;
}

.hero__content {
  position: relative;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  padding: 55px 55px 100px 55px;
  background-image: url("../img/hero-1.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #939699;
  z-index: 10;
}

.hero__content--2 {
  background-image: url("../img/news-bg.jpg");
}

.hero__content--3 {
  background-image: url("../img/news-bg-2.jpg");
}

.hero__content--4 {
  background-image: url("../img/news-bg-3.jpg");
}

.hero__content--5 {
  background-image: url("../img/news-bg-5.jpg");
}

.hero__content--6 {
  background-image: url("../img/news-bg-4.jpg");
}

.hero__content::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, rgba(64, 48, 61, 0.35), rgba(64, 48, 61, 0.35));
  z-index: -1;
}

.hero__content--active {
  display: flex;
}

.hero__content-subtitle {
  margin-bottom: 21px;
  font-weight: 900;
  font-size: 12px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #4592ff;
}

.hero__content-title {
  max-width: 70%;
  margin-bottom: 49px;
  font-family: 'Roboto Slab', sans-serif;
  font-size: 40px;
  line-height: 50px;
  color: #ffffff;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.hero__sidebar {
  height: 100%;
  max-width: 263px;
  background-color: #fff;
}

.hero__sidebar-title {
  padding: 23px 25px 20px;
  font-weight: 900;
  font-size: 14px;
  color: #262D33;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.hero__sidebar-item {
  height: 100px;
  border-top: 1px solid #D9DADB;
}

.hero__sidebar-item:last-child div {
  height: 112%;
}

.hero__sidebar-link {
  padding: 19px 25px 19px;
  display: block;
  height: 100%;
  cursor: pointer;
}

.hero__sidebar-link--active {
  background-color: #F5F5F5;
}

.hero__sidebar-link:hover {
  background-color: #F5F5F5;
}

.hero__sidebar-subtitle {
  margin-bottom: 9px;
  font-weight: 900;
  font-size: 12px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.hero__sidebar-subtitle--1 {
  color: #3BBDC4;
}

.hero__sidebar-subtitle--2 {
  color: #6E99AE;
}

.hero__sidebar-subtitle--3 {
  color: #AC8EE3;
}

.hero__sidebar-subtitle--4 {
  color: #FFA34D;
}

.hero__sidebar-subtitle--5 {
  color: #3DC47E;
}

.hero__sidebar-text {
  font-size: 14px;
  line-height: 20px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.footer {
  background-color: #fff;
}

.footer__menu {
  display: flex;
  padding-bottom: 65px;
}

.footer__inner {
  flex-basis: 195px;
}

.footer__inner:last-child {
  flex-basis: 165px;
}

.footer__inner--1 {
  border-top: 5px solid #FF4F52;
}

.footer__inner--2 {
  border-top: 5px solid #AC8EE3;
}

.footer__inner--3 {
  border-top: 5px solid #4592FF;
}

.footer__inner--4 {
  border-top: 5px solid #FFA34D;
}

.footer__inner--5 {
  border-top: 5px solid #3DC47E;
}

.footer__inner--6 {
  border-top: 5px solid #6E99AE;
}

.footer__title {
  font-weight: 900;
  font-size: 14px;
  color: #262D33;
  margin-bottom: 27px;
  margin-top: 33px;
}

.footer__logo-link {
  width: 50px;
  height: 50px;
  margin-right: 30px;
}

.footer__item:not(:last-child) {
  margin-bottom: 17px;
}

.footer__link {
  font-size: 14px;
  color: #4b5157;
  position: relative;
}

.footer__social {
  display: flex;
  align-items: center;
  padding: 30px 0 29px;
  border-top: 1px solid #D9DADB;
  border-bottom: 1px solid #D9DADB;
}

.footer__social-menu {
  display: flex;
}

.footer__social-item:not(:last-child) {
  margin-right: 30px;
}

.footer__social-link {
  position: relative;
  font-weight: 900;
  font-size: 14px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #262D33;
  padding: 0;
  white-space: nowrap;
}

.footer__media {
  margin-left: auto;
}

.footer__copy {
  display: flex;
  justify-content: space-between;
  padding-top: 41px;
  padding-bottom: 55px;
}

.footer__copy-descr {
  font-size: 14px;
  line-height: 20px;
  color: #939699;
  width: 70%;
}

.footer__copy-text {
  font-size: 14px;
  line-height: 20px;
  color: #939699;
}

@media (hover: hover) and (pointer: fine) {
  .footer__link::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: -5px;
    width: 0;
    height: 1px;
    background-color: black;
    transition: color 0.3s ease, width 0.3s ease;
  }
  .footer__link:hover {
    color: black;
    transition: color 0.3s ease;
  }
  .footer__link:hover::after {
    width: 100%;
    transition: width 0.3s ease;
  }
  .footer__social-link::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: -5px;
    width: 0;
    height: 1px;
    background-color: black;
    transition: color 0.3s ease, width 0.3s ease;
  }
  .footer__social-link:hover {
    color: black;
    transition: color 0.3s ease;
  }
  .footer__social-link:hover::after {
    width: 100%;
    transition: width 0.3s ease;
  }
}

.user {
  display: flex;
  align-items: center;
}

.user__img {
  width: 45px;
  height: 45px;
  margin-right: 10px;
  border-radius: 50%;
}

.user__info {
  display: flex;
  flex-direction: column;
}

.user__name {
  margin-bottom: 5px;
  font-size: 14px;
  color: #ffffff;
  mix-blend-mode: normal;
  opacity: 0.85;
}

.user__name--dark {
  color: #4b5157;
}

.user__job {
  font-size: 12px;
  color: #ffffff;
  opacity: 0.4;
}

.user__job--dark {
  color: #939699;
}

.button {
  padding: 10px 40px 9px 20px;
  min-width: 154px;
  height: 40px;
  font-weight: 900;
  font-size: 14px;
  color: #ffffff;
  background-color: #4592ff;
  border-radius: 20px;
}

.button__link {
  display: inline-block;
  padding: 13px 40px 11px 20px;
}

.button__arrow {
  position: relative;
  text-align: left;
}

.button__arrow::after {
  content: "";
  position: absolute;
  right: 22px;
  top: 50%;
  transform: translatey(-50%);
  width: 15px;
  height: 7px;
  background-image: url("../img/btn-arrow.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

@media (hover: hover) and (pointer: fine) {
  .button {
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
  }
  .button:hover {
    background-color: #0066f8;
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
    box-shadow: 0 1px 3px #4592ff;
  }
}

.article__list {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
  background-color: #30363D;
  border-radius: 10px;
  overflow: hidden;
}

.article__item {
  position: relative;
}

.article__item::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  width: 1px;
  height: 80px;
  background-color: #4b5157;
}

.article__item:last-child::after {
  display: none;
}

.article__link {
  position: relative;
  display: flex;
  align-items: center;
  padding: 30px 25px 35px;
}

.article__title {
  font-weight: 900;
  font-size: 14px;
  max-width: 154px;
  line-height: 20px;
  color: #ffffff;
  margin-right: 15px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.article__img {
  width: 65px;
  height: 65px;
  object-fit: cover;
}

@media (hover: hover) and (pointer: fine) {
  .article__link {
    transition: background-color 0.3s ease;
  }
  .article__link:hover {
    background-color: #3b434b;
    transition: background-color 0.3s ease;
  }
}

.grid__list {
  display: grid;
  gap: 30px;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 170px;
}

.grid__item {
  background-color: #ffffff;
  border-radius: 10px;
  overflow: hidden;
}

.grid__item--big {
  grid-column: span 2;
  grid-row: span 2;
  background-image: url("../img/grid-big.png");
  background-repeat: no-repeat;
  background-position: right 15px;
}

.grid__item--mid {
  grid-row: span 2;
}

.grid__item--social {
  padding: 30px 36px 40px 37px;
  text-align: center;
}

.grid__item--small {
  position: relative;
  padding: 25px;
  background-color: #3BBDC4;
  background-image: url("../img/file.svg");
  background-repeat: no-repeat;
  background-position: right 22px top 24px;
}

.grid__comment {
  margin-left: auto;
}

.grid__info {
  max-width: 237px;
}

.grid__subtitle {
  margin-bottom: 23px;
  font-size: 12px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #6E99AE;
}

.grid__subtitle--mid {
  font-size: 14px;
  color: #262D33;
  margin-bottom: 0;
  border-bottom: 1px solid #D9DADB;
  padding: 23px 26px 20px;
}

.grid__subtitle--purple {
  color: #E7D9FF;
}

.grid__title {
  font-family: 'Roboto Slab', sans-serif;
  font-size: 25px;
  line-height: 30px;
  color: #262D33;
}

.grid__title--small {
  margin-bottom: 25px;
  font-size: 20px;
  line-height: 25px;
}

.grid__title--mid {
  align-self: flex-end;
  font-size: 16px;
  line-height: 25px;
