.modal__checkbox + .modal__label::before {
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  flex-grow: 0;
  margin-left: -10px;
  border: 1px solid #adb5bd;
  border-radius: 3px;
  margin-right: 11px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
  cursor: pointer;
}

.modal__checkbox:checked + .modal__label::before {
  background-color: #4592ff;
  background-image: url("../img/check.svg");
  border: none;
}

