@mixin transfer4 {
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden; }

@mixin transfer3 {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden; }

@mixin transfer2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden; }

@mixin transfer1 {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

@mixin link-hover {
  &::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: -5px;
    width: 0;
    height: 1px;
    background-color: darken($dark-color,20%);
    transition: color 0.3s ease, width 0.3s ease; }
  &:hover {
    color: darken($dark-color,20% );
    transition: color 0.3s ease; }
  &:hover::after {
    width: 100%;
    transition: width 0.3s ease; } }
