.grid {
  &__list {
    display: grid;
    gap: 30px;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: 170px; }
  &__item {
    background-color: $light-color;
    border-radius: 10px;
    overflow: hidden;
    &--big {
      grid-column: span 2;
      grid-row: span 2;
      background-image: url("../img/grid-big.png");
      background-repeat: no-repeat;
      background-position: right 15px; }
    &--mid {
      grid-row: span 2; }
    &--social {
      padding: 30px 36px 40px 37px;
      text-align: center; }
    &--small {
      position: relative;
      padding: 25px;
      background-color: #3BBDC4;
      background-image: url("../img/file.svg");
      background-repeat: no-repeat;
      background-position: right 22px top 24px; } }
  &__comment {
    margin-left: auto; }
  &__info {
    max-width: 237px; }
  &__subtitle {
    margin-bottom: 23px;
    font-size: 12px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: #6E99AE;
    &--mid {
      font-size: 14px;
      color: $dark-color;
      margin-bottom: 0;
      border-bottom: 1px solid #D9DADB;
      padding: 23px 26px 20px; }
    &--purple {
      color: #E7D9FF; } }
  &__title {
    font-family: 'Roboto Slab', sans-serif;
    font-size: 25px;
    line-height: 30px;
    color: $dark-color;
    &--small {
      margin-bottom: 25px;
      font-size: 20px;
      line-height: 25px; }
    &--mid {
      align-self: flex-end;
      font-size: 16px;
      line-height: 25px;
      @include transfer2; }
    &--big {
      margin-bottom: 25px;
      @include transfer3; }
    &--check {
      margin-bottom: 11px;
      font-size: 16px;
      line-height: 25px;
      color: $light-color;
      @include transfer2; }
    &--full {
      font-size: 16px;
      line-height: 25px;
      color: $light-color;
      letter-spacing: 0.5px;
      text-transform: uppercase;
      margin-bottom: 27px;
      @include transfer3; } }
  &__download {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 13px;
    padding-bottom: 15px;
    text-align: center;
    font-weight: 900;
    font-size: 14px;
    color: $light-color;
    border-top: 1px solid #96E2E3;
    &-img {
      width: 17px;
      height: 17px;
      margin-right: 11px; } }
  &__file {
    font-size: 14px;
    color: #C1F4F5; }
  &__text {
    font-size: 14px;
    line-height: 20px;
    @include transfer4; }
  &__tags {
    display: flex;
    flex-wrap: wrap;
    padding: 25px;
    padding-right: 20px; }
  &__tag {
    margin-bottom: 13px;
    margin-right: 9px;
    &-link {
      display: inline-block;
      padding: 7px 15px;
      font-weight: 900;
      font-size: 14px;
      color: $dark-color;
      border: 1px solid #D9DADB;
      border-radius: 5px; } }
  &__link {
    position: relative;
    display: block;
    height: 100%;
    width: 100%;
    &--big {
      padding: 35px;
      padding-bottom: 90px; }
    &--mid {
      display: flex;
      background-image: url("../img/grid-mid.jpg");
      background-repeat: no-repeat;
      background-position: top 95px;
      padding: 30px 52px 30px 25px; }
    &--full {
      position: relative;
      z-index: 10;
      display: flex;
      justify-content: flex-end;
      flex-direction: column;
      padding: 25px;
      background-image: url("../img/grid-full.jpg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      &::after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(0deg, rgba(64, 48, 61, 0.35), rgba(64, 48, 61, 0.35));
        z-index: -1; } } }
  &__user {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    padding: 12px 35px 13px;
    border-top: 1px solid #D9DADB;
    &-info {
      display: flex; }
    &-img {
      width: 30px;
      height: 30px;
      margin-right: 10px;
      border-radius: 50%; }
    &-name {
      margin-right: 5px;
      font-weight: 900;
      font-size: 14px;
      color: $dark-color; }
    &-text {
      font-size: 14px;
      @include transfer1; } } }

.tagname {
  position: absolute;
  left: 24px;
  top: 19px;
  padding: 6px 12px;
  display: inline-block;
  font-size: 12px;
  color: $light-color;
  background-color: #3DC47E;
  border-radius: 3px; }

.comment {
  display: flex;
  align-items: center;
  &--mr {
    margin-right: 8px; }
  &__img {
    width: 13.5px;
    height: 13.5px;
    margin-right: 6px;
    &--white {
      fill: #fff;
      opacity: 0.9; } }
  &__counter {
    font-size: 12px;
    color: $grey-color;
    &--light {
      color: $light-color; } } }

.like {
  display: flex;
  align-items: center;
  &__img {
    width: 13.5px;
    height: 13.5px;
    margin-right: 6px;
    &--white {
      fill: #fff;
      opacity: 0.9; } }
  &__counter {
    font-size: 12px;
    color: $light-color; } }

.user {
  &__descr {
    color: $light-color;
    opacity: 0.4;
    display: flex; }
  &__date {
    font-size: 12px;
    margin-right: 8px; } }

@media(hover: hover) and (pointer: fine) {
  .grid {
    &__tag {
      &-link {
        transition: background-color 0.3s ease, color 0.3s ease;
        &:hover {
          background-color: lighten(#30363D, 30%);
          transition: background-color 0.3s ease, color 0.3s ease;
          color: $light-color; } } }
    &__download {
      transition: background-color 0.3s ease;
      &:hover {
        background-color: darken(#96E2E3, 35%);
        transition: background-color 0.3s ease; } }

    &__item {
      &--big,&--mid {
        transition: box-shadow 0.3s ease;
        &:hover {
          transition: box-shadow 0.3s ease;
          box-shadow: 0 3px 6px rgba(0,0,0, 0.5); } } } } }
